import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PopupModule } from '@progress/kendo-angular-popup';
import { ChartAutoThemeComponent } from './chart/chart-auto-theme.component';
import { CldrIntlService, IntlService } from '@progress/kendo-angular-intl';
import { ThemeService } from './common/theme.service';
import { TooltipTemplateService } from './common/tooltip-template.service';
import { CHART_DIRECTIVES } from './chart.directives';

/**
 * Root ngModule for the Charts.
 */
@NgModule({
  declarations: [CHART_DIRECTIVES],
  entryComponents: [ChartAutoThemeComponent],
  exports: [CHART_DIRECTIVES],
  imports: [CommonModule, PopupModule],
  providers: [
    { provide: IntlService, useClass: CldrIntlService },
    ThemeService,
    TooltipTemplateService
  ]
})
export class ChartsModule {
}



